import React from 'react';
import Typography from '@material-ui/core/Typography';
import { l10n_text } from '../../components/L10n';

class PlayerInfoComponent extends React.Component {
  render() {
    const { user, style } = this.props;
    return (
      <div style={style}>
        <Typography variant="caption">
           
          <Typography>{user.startedPlaying ? user.startedPlaying + '.' : ''}</Typography>
        </Typography>

        <Typography variant="caption">
           
          <Typography>{l10n_text(user.playStyle, "PlayStyle") || ''}</Typography>
        </Typography>

        <Typography variant="caption">
          
          <Typography>{l10n_text(user.backhandType, "BackhandType") || ''}</Typography>
        </Typography>

        <Typography variant="caption">
           
          <Typography>{l10n_text(user.courtType, "CourtType") || ''}</Typography>
        </Typography>
      </div>
    );
  }
}

export default PlayerInfoComponent;